import React from "react";
import { Spin } from "antd";

function Loader() {
  return (
    <div
      style={{
        minHeight: "100vh",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        background: "#dfdbe5",
      }}
    >
      <Spin size="large" />
    </div>
  );
}

export default Loader;
