import React from "react";
import { Route, Switch } from "react-router-dom";
import Auth from "../hoc/auth";
// pages for this product
import NavBar from "./views/NavBar/NavBar";
import Footer from "./views/Footer/Footer";
import Loader from "./utils/Loader";
import { BackTop } from "antd";
import { UpSquareOutlined } from "@ant-design/icons";
import ScrollIntoView from "./utils/ScrollIntoView";

//null   Anyone Can go inside
//true   only logged in user can go inside
//false  logged in user can't go inside

const LandingPage = React.lazy(() => import("./views/LandingPage/LandingPage"));
const SearchPage = React.lazy(() => import("./views/SearchPage/SearchPage"));
const UploadProductPage = React.lazy(() =>
  import("./views/UploadProductPage/UploadProductPage")
);
const DetailProductPage = React.lazy(() =>
  import("./views/DetailProductPage/DetailProductPage")
);
const CartPage = React.lazy(() => import("./views/CartPage/CartPage"));
const HistoryPage = React.lazy(() => import("./views/HistoryPage/HistoryPage"));
const PrivateTrips = React.lazy(() =>
  import("./views/PrivateTrips/PrivateTrips")
);
const ContactPage = React.lazy(() => import("./views/ContactPage/ContactPage"));
const Fallback = React.lazy(() => import("./views/Fallback/Fallback"));

function App() {
  return (
    <ScrollIntoView>
      <React.Suspense fallback={<Loader />}>
        <NavBar />
        <Switch>
          <Route exact path="/" component={Auth(LandingPage, null)} />
          <Route path="/search" component={Auth(SearchPage, null)} />
          <Route
            path="/product/upload"
            component={Auth(UploadProductPage, true)}
          />
          <Route
            path="/product/:productId"
            component={Auth(DetailProductPage, null)}
          />
          <Route path="/user/cart" component={Auth(CartPage, true)} />
          <Route path="/history" component={Auth(HistoryPage, true)} />
          <Route path="/private" component={Auth(PrivateTrips, null)} />
          <Route path="/contact" component={Auth(ContactPage, null)} />
          <Route path="/login" component={Auth(Fallback, null)} />
        </Switch>
        <BackTop>
          <div>
            <UpSquareOutlined />
          </div>
        </BackTop>
        <Footer />
      </React.Suspense>
    </ScrollIntoView>
  );
}

export default App;
