/* eslint-disable jsx-a11y/anchor-is-valid */
import React from "react";
import { Menu } from "antd";
import axios from "axios";
import { USER_SERVER } from "../../../Config";
import { useSelector } from "react-redux";
import { Avatar } from "antd";
import {
  ShoppingCartOutlined,
  UploadOutlined,
  LogoutOutlined,
  HistoryOutlined,
} from "@ant-design/icons";
import { Badge } from "antd";
import LoginModal from "../../LoginPage/LoginModal";
import { Link } from "react-router-dom";

const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;

function RightMenu(props) {
  const user = useSelector((state) => state.user);

  const logoutHandler = () => {
    axios.get(`${USER_SERVER}/logout`).then((response) => {
      if (response.status === 200) {
        window.location.reload(false);

        // props.history.push("/");
      } else {
        alert("Log Out Failed");
      }
    });
  };

  if (user.userData && !user.userData.isAuth) {
    return (
      <Menu mode={props.mode}>
        <Menu.Item key="mail" className="navModal">
          <LoginModal />
        </Menu.Item>
      </Menu>
    );
  } else {
    return (
      <Menu mode={props.mode}>
        <SubMenu
          popupOffset={[-120, 14]}
          title={
            <span>
              {" "}
              <Badge
                count={user.userData && user.userData.cart.length}
                style={{ background: "#1890ff" }}
              >
                <Avatar size={40} src={user.userData && user.userData.image} />
              </Badge>
            </span>
          }
        >
          <MenuItemGroup
            title={<span>Welcome {user.userData && user.userData.name}</span>}
          >
            <Menu.Item key="upload">
              <Link to="/product/upload">
                <UploadOutlined /> Upload Product
              </Link>
            </Menu.Item>
            <Menu.Item key="cart">
              <Badge count={user.userData && user.userData.cart.length}>
                {" "}
                <Link to="/user/cart">
                  <ShoppingCartOutlined /> Cart
                </Link>
              </Badge>
            </Menu.Item>{" "}
            <Menu.Item key="history">
              <Link to="/history">
                <HistoryOutlined /> History
              </Link>
            </Menu.Item>
            <Menu.Item key="logout">
              <Link to="" onClick={logoutHandler}>
                <LogoutOutlined /> Logout
              </Link>
            </Menu.Item>
          </MenuItemGroup>
        </SubMenu>
      </Menu>
    );
  }
}

export default RightMenu;
